import React, { Component } from "react";
import { Redirect } from 'react-router-dom';
import Form from "react-validation/build/form";
import Input from "react-validation/build/input";
import Select from "react-validation/build/select";
import CheckButton from "react-validation/build/button";
import Header from './Header';
import Menu from './Menu';
import Footer from './Footer';
import axios from 'axios'


const required = value => {
  if (!value) {
      return (
          <div className="alert alert-danger" role="alert">
              This field is required!
          </div>
      );
  }
};

export default class Deploy extends React.Component {
  constructor(props) {

    super(props);
        this.onChangeTitle = this.onChangeTitle.bind(this);
        this.onChangeQuarter = this.onChangeQuarter.bind(this);
        this.onChangeSurveyType = this.onChangeSurveyType.bind(this);
        this.onChangeYear = this.onChangeYear.bind(this);
        this.CreateSurvey=this.CreateSurvey.bind(this);
    this.state = {
      title: "",
      quarter: "",
      year: "",
      survey_type: "",
      message: "",
      successful: false,
      loading: false
    };

  }

  onChangeTitle(e) {
        this.setState({
            title: e.target.value
        });
    }

    onChangeQuarter(e) {
        this.setState({
            quarter: e.target.value
        });
    }

    onChangeYear(e) {
        this.setState({
            year: e.target.value
        });
    }
   
    onChangeSurveyType(e) {
        this.setState({
            survey_type: e.target.value
        });
    }

    

    
  
    CreateSurvey(e) {
      e.preventDefault();

      this.setState({
          message: "",
          successful: false,
          loading: true
      });

      
          //check auth user
          const mytoken = JSON.parse(localStorage.getItem('user'));
          const token = mytoken.token;

       
         
          // newly created json
          const body =
          {
            title: this.state.title,
            quarter: this.state.quarter,
            year: this.state.year,
            survey_type: this.state.survey_type
            
          } 
          // console.log(body);
          this.form.validateAll();
          if (this.checkBtn.context._errors.length === 0) {
  
          axios.post("https://gfso.chinikiguard.com/survey/api/create/survey/", body,
              {
                  headers:
                  {
                      'Content-Type': 'application/json',
                      'Authorization': `Token ${token}`,
                      'Access-Control-Allow-Origin': '*',
                      'Access-Control-Allow-Methods': 'GET,POST,HEAD,OPTIONS',
                      'Access-Control-Allow-Credentials': true
                  },
              })
              .then(res => {
                  this.setState({
                      message: res.data.detail,
                      successful: true,
                  });
                  window.location = "/Deploy"
                  if (res.data) {
                      localStorage.setItem("CreatedSurvey", JSON.stringify(res.data));
                  }
              },
                  error => {
                      const resMessage =
                          (error.response &&
                              error.response.data.detail &&
                              error.response.data.detail) ||
                          error.loading ||
                          error.toString();

                      this.setState({
                          loading: false,
                          message: resMessage
                      });
                  }
              );
      } else {
          this.setState({
              loading: false
          });
      }
  }

  componentDidUpdate(prevProps, prevState) {
    //reset to default state
    const Checked_State = [1, 1, 1]
    localStorage.setItem("Checked_State", Checked_State);
    const Checked_LGA = [1, 1, 1]
    localStorage.setItem("Checked_LGA", Checked_LGA);
    // check for the value selected and show the related content
    try {
        if (prevState.survey_type !== this.state.survey_type) {
            const state = this.state.survey_type
            if (state) {
                this.setState({
                    statesGov: state.includes("SG"),
                    lgasGov: state.includes("LG"),
                    othersGov: state.includes("OTHER"),
                });
            }
        }

    } catch (e) {

        console.log(e);
    }
}

  render() {


    if (!localStorage.getItem('user')) {

      return (<Redirect to={'/login'} />)
    }

    return (
      <div>
        <Header />
        <Menu />
        <div className="content-wrapper">
          <br />
          <section className="content">
            <div className="container-fluid">
              <div className="row">
                <div className="col-12">
                  <div className="card">
                    <div className="card-header">
                      <h3 className="card-title"> Deploy Survey</h3>
                      <div className="card-tools">
                      </div>
                    </div>
                    <div className="card-body">
                      <Form onSubmit={this.CreateSurvey} ref={c => { this.form = c; }} >
                        <div className="row">
                          <div className="col-sm-6">
                            <div className="form-group">
                              <label> Title</label>
                              <Input type="text" className="form-control " placeholder
                                value={this.state.title}
                                onChange={this.onChangeTitle}
                                validations={[required]}
                              />
                            </div>
                          </div>
                          <div className="col-sm-6">
                            <div className="form-group">
                              <label> Quarter</label>
                              <Select className="form-control " placeholder
                                value={this.state.quarter}
                                onChange={this.onChangeQuarter}
                                validations={[required]}
                              >
                                <option value="">Select</option>
                                <option value="q1">Q1</option>
                                <option value="q2">Q2</option>
                                <option value="q3">Q3</option>
                                <option value="q4">Q4</option>
                              </Select>
                            </div>
                          </div>
                        </div>
                        <div className="row">
                          <div className="col-sm-6">
                            <div className="form-group">
                              <label> Year</label>
                              <Input type="number" className="form-control " placeholder
                                value={this.state.year}
                                onChange={this.onChangeYear}
                                validations={[required]}
                              />
                            </div>
                          </div>   
                          <div className="col-sm-6">
                            <div className="form-group">
                              <label> Survey Type</label>
                              <Select type="text" className="form-control " placeholder
                                validations={[required]}
                                value={this.state.survey_type}
                                onChange={this.onChangeSurveyType}
                              >
                                <option value="">Select</option>
                                <option value="sg">SG</option>
                                <option value="lg">LG</option>
                                <option value="others">OTHER</option>
                              </Select>
                            </div>
                          </div>
                        </div>  
                        <center>
                          {this.state.message && (
                            <div className="pb-5" >
                              <div
                                className={
                                  this.state.successful
                                    ? "alert alert-custom success "
                                    : "alert alert-custom danger "
                                }
                                role="alert"
                              >
                                <h3 > {this.state.message}</h3>
                              </div>
                            </div>
                          )}</center>
                        <center>
                          <div className="row">
                            <button
                              disabled={this.state.loading}
                              className="btn btn-success btn-lg float-right"
                            >

                              {this.state.loading && (
                                <span className="spinner-grow spinner-grow-sm"></span>
                              )}
                              <span>Create Survey</span>
                            </button>
                          </div>
                        </center>
                        <CheckButton
                          style={{ display: "none" }}
                          ref={c => {
                            this.checkBtn = c;
                          }}
                        />
                      </Form>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </section>
        </div>
      </div>
    );
  }
}
