export default class CreateNewUser extends Component {

  constructor(props) {
    super(props);
    this.handleRegister = this.handleRegister.bind(this);
    this.onChangeFirstName = this.onChangeFirstName.bind(this);
    this.onChangeLastName = this.onChangeLastName.bind(this);
    this.onChangeAddress = this.onChangeAddress.bind(this);
    this.onChangeLG = this.onChangeLG.bind(this);
    this.onChangeState = this.onChangeState.bind(this);
    this.onChangeUsername = this.onChangeUsername.bind(this);
    this.onChangeEmail = this.onChangeEmail.bind(this);
    this.onChangePassword = this.onChangePassword.bind(this);
    this.onChangeDob = this.onChangeDob.bind(this);
    this.onChangePhoneNumber = this.onChangePhoneNumber.bind(this);



    this.state = {
      username: "",
      password: "",
      first_name: "",
      last_name: "",
      address: "",
      lga: "",
      states: "",
      dob: "",
      email: "",
      phone_number: "",
      successful: false,
      message: "",
      loading: false

    };
  }


  componentDidMount() {
    if (localStorage.getItem('user')) {

      return (<Redirect to={'/dashboard'} />)
    }
    const script = document.createElement("script");

    script.src = "/plugins/daterangepicker/daterangepicker.js";
    script.async = true;

    document.body.appendChild(script);
    const mytoken = JSON.parse(localStorage.getItem('user'));
    const token = mytoken.token;
    //call the States and LGAS at the first 
    const state = "Ahmad loophole";
    axios.get(`https://gfso.chinikiguard.com/locations/api/list/state/`,
      {
        headers:
        {
          'Content-Type': 'application/json',
          'Authorization': `Token ${token}`,
          'Access-Control-Allow-Origin': '*',
          'Access-Control-Allow-Methods': 'GET,POST,HEAD,OPTIONS',
          'Access-Control-Allow-Credentials': true
        },
        params:
        {
          state: state
        }
      },
    )
      .then(res => {
        if (res.data) {
          localStorage.setItem("List_Of_State", JSON.stringify(res.data));
        }
        // console.log(res.data)                  

      })
    axios.get(`https://gfso.chinikiguard.com/locations/api/list/lga/`,
      {
        headers:
        {
          'Content-Type': 'application/json',
          'Authorization': `Token ${token}`,
          'Access-Control-Allow-Origin': '*',
          'Access-Control-Allow-Methods': 'GET,POST,HEAD,OPTIONS',
          'Access-Control-Allow-Credentials': true
        },
        params:
        {
          state: state
        }
      },
    )
      .then(res => {
        if (res.data) {
          localStorage.setItem("List_Of_Lga", JSON.stringify(res.data));
        }
      });




  }

  componentDidUpdate(prevProps, prevState) {

    // const GetUsers = JSON.parse(localStorage.getItem('Users'));
    const list_of_lga = JSON.parse(localStorage.getItem('List_Of_Lga'))
    const statepk = this.state.states

    if (statepk) {
      const Lgas = list_of_lga.filter(item => item.state === statepk);
      localStorage.setItem("LGAS", JSON.stringify(Lgas));
    }

    const mytoken = JSON.parse(localStorage.getItem('user'));
    const token = mytoken.token;

    axios.get("https://gfso.chinikiguard.com/accounts/api/users/",
      {
        headers:
        {
          'Authorization': `Token ${token}`,
          'Access-Control-Allow-Origin': '*',
          'Access-Control-Allow-Methods': 'GET,POST,HEAD,OPTIONS',
          'Access-Control-Allow-Credentials': true
        },
      })
      .then(res => {
        if (res.data) {
          localStorage.setItem("Users", JSON.stringify(res.data));
        }
        // (res);
        // (res.data);

      })

  }

  onChangeFirstName(e) {
    this.setState({
      first_name: e.target.value
    });
  }
  onChangeLastName(e) {
    this.setState({
      last_name: e.target.value
    });
  }
  onChangeAddress(e) {
    this.setState({
      address: e.target.value
    });
  }
  onChangeLG(e) {
    this.setState({
      lga: e.target.value
    });
  }
  onChangeState(e) {
    this.setState({
      states: e.target.value
    });
  }

  onChangeUsername(e) {
    this.setState({
      username: e.target.value
    });
  }

  onChangeEmail(e) {
    this.setState({
      email: e.target.value
    });
  }

  onChangePassword(e) {
    this.setState({
      password: e.target.value
    });
  }
  onChangeDob(e) {
    this.setState({
      dob: e.target.value
    });
  }
  onChangePhoneNumber(e) {
    this.setState({
      phone_number: e.target.value
    });
  }

  handleRegister(e) {
    e.preventDefault();
    this.setState({
      message: "",
      successful: false,
      loading: true
    });
    this.form.validateAll();
    if (this.checkBtn.context._errors.length === 0) {
      AuthService.createNewUser(
        this.state.username,
        this.state.password,
        this.state.first_name,
        this.state.last_name,
        this.state.address,
        this.state.lga,
        this.state.states,
        this.state.dob,
        this.state.email,
        this.state.phone_number,

      ).then(
        response => {
          this.setState({
            message: response.data.message,
            successful: true
          });
          window.location = "/register"

        },
        error => {
          const resMessage =
            (error.response &&
              error.response.data.detail &&
              error.response.data.detail) ||
            error.message ||
            error.toString();

          this.setState({
            loading: false,
            message: resMessage
          });
        }
      );
    } else {
      this.setState({
        loading: false
      });
    }

  }
  render() {

    // const { loading } = this.state;

    const list_of_state = JSON.parse(localStorage.getItem('List_Of_State'))
    const list_of_lga = JSON.parse(localStorage.getItem('LGAS'))
    // console.log(list_of_lga);
    const Users = JSON.parse(localStorage.getItem('Users'))




    return (
      <div>
        <Header />

        <div className="content-wrapper" >
          <Menu />
          <section className="content-header">
            <div className="container-fluid">
              <center><h1>User Management</h1></center>
            </div>{/* /.container-fluid */}
          </section>

          <div className="col-md-12" >
            <br />

            <div className="card card-" >
              {/* /.card-header */}
              <div className="card-body">
                <Form onSubmit={this.handleRegister} ref={c => { this.form = c; }} className="form" id="">
                  <div className="row">
                    <div className="col-sm-6">
                      {/* text input */}
                      <div className="form-group">
                        <label>First Name</label>
                        <Input type="text" id="firstname" className="form-control" placeholder="first name" value={this.state.first_name}
                          onChange={this.onChangeFirstName}
                          validations={[required]} />
                      </div>
                    </div>
                    <div className="col-sm-6">
                      <div className="form-group">
                        <label>Last Name</label>
                        <Input type="text" id="lastname" className="form-control" placeholder="last name" value={this.state.last_name}
                          onChange={this.onChangeLastName}
                          validations={[required]} />
                      </div>
                    </div>
                  </div>

                  <div className="row">
                    <div className="col-sm-6">
                      {/* textarea */}
                      <div className="form-group">
                        <label>Email</label>
                        <Input type="email" id="email" className="form-control" placeholder="Email" value={this.state.email}
                          onChange={this.onChangeEmail}
                          validations={[required]} />
                      </div>
                    </div>
                    <div className="col-sm-6">
                      <div className="form-group">
                        <label>Phone Number</label>
                        <Input type="tel" id="phonenumber" className="form-control" placeholder="Phone number" value={this.state.phone_number}
                          onChange={this.onChangePhoneNumber}
                          validations={[required]} />
                      </div>
                    </div>
                  </div>

                  <div className="row">
                    <div className="col-sm-6">
                      {/* text input */}
                      <div className="form-group">
                        <label>Address</label>
                        <Textarea type="text" id="address" className="form-control"
                          placeholder="Address" value={this.state.address}
                          onChange={this.onChangeAddress} validations={[required]} />
                      </div>
                    </div>
                    <div className="col-sm-6">
                      <div className="form-group">

                        <label>Date of birth</label>

                        <Input type="date" format="YYYY-MM-DD" className="form-control " placeholder
                          value={this.state.dob}
                          onChange={this.onChangeDob}

                        />
                      </div>
                    </div>
                  </div>

                  <div className="row">
                    <div className="col-sm-6">
                      <div className="form-group">
                        <label>State</label>
                        <Select value={this.state.states}
                          onChange={this.onChangeState}
                          validations={[required]} type="text" id="state" className="form-control">
                          <option value>Select</option>
                          {list_of_state.map(state => (

                            <option value={state.pk}>{state.title}</option>

                          ))}

                        </Select>
                      </div>
                    </div>

                    <div className="col-sm-6">
                      <div className="form-group">
                        <label>Lga</label>
                        <Select value={this.state.lga}
                          onChange={this.onChangeLG}
                          validations={[required]} type="text" id="state" className="form-control">
                          <option value>Select</option>
                          {list_of_lga && list_of_lga.map(lga => (
                            <option value={lga.pk}>{lga.title}</option>
                          ))}
                        </Select>

                      </div>
                    </div>
                  </div>
                  <div className="row">
                    <div className="col-sm-6">
                      <div className="form-group">
                        <label>Username</label>
                        <Input type="text" name="username" className="form-control" placeholder="Username"
                          value={this.state.username}
                          onChange={this.onChangeUsername}
                          validations={[required]} />
                      </div>
                    </div>
                    <div className="col-sm-6">
                      <div className="form-group">
                        <label>Password</label>
                        <Input type="password" name="password" className="form-control" placeholder="password" value={this.state.password}
                          onChange={this.onChangePassword}
                          validations={[required]} />
                      </div>
                    </div>
                  </div>
                  <div className="form-group">
                    <button
                      className="btn btn-success btn-lg float-right"
                      disabled={this.state.loading}
                    >
                      {this.state.loading && (
                        <span className="spinner-border spinner-border-sm"></span>
                      )}
                      <span>Create User +</span>
                    </button>
                  </div>
                  <center>
                    {this.state.message && (
                      <div className="form-group">
                        <div
                          className={
                            this.state.successful
                              ? "alert alert-success"
                              : "alert alert-danger"
                          }
                          role="alert"
                        >
                          {this.state.message}
                        </div>
                      </div>
                    )}
                  </center>

                  <CheckButton
                    style={{ display: "none" }}
                    ref={c => {
                      this.checkBtn = c;
                    }}
                  />

                </Form>
              </div>
              {/* /.card-body */}
            </div>
          </div>

          <section className="content">
            <div className="row">
              <div className="col-12">
                <div className="card">
                  <div className="card-header">
                    <h3 className="card-title">List of Users</h3>
                    <div className="card-tools">

                    </div>
                  </div>
                  {/* /.card-header */}

                  <div className="card-body table-responsive p-0">

                    <table className="table table-hover text-nowrap">
                      <thead>
                        <tr>
                          <th>Name</th>
                          <th>Email</th>
                          <th>Job Role</th>
                          <th>Registered on</th>
                          <th>Last Login</th>
                        </tr>
                      </thead>
                      {Users && Users.map(users => (

                        <tbody>
                          <tr>
                            <td>{users.first_name}&nbsp;{users.last_name}</td>
                            <td>{users.email}</td>
                            <td>{users.groups}</td>
                            <td>{users.date_joined}</td>
                            <td>{users.last_login}</td>
                          </tr>

                        </tbody>
                      ))}
                    </table>
                  </div>


                  {/* /.card-body */}
                </div>
                {/* /.card */}
              </div>
            </div>
          </section>


        </div>
        <Footer />

      </div>


    );
  }
}